.footer {
    background-color: #2D2D2D;
    padding-top: 80px;
}

.footer__title {
    min-width: 114px;
    min-height: 25px;
    font-weight: 700;
    font-size: 18px;
    line-height: 25px;
    letter-spacing: 0.09em;
    color: #FFFFFF;
}

.footer__name--blue {
    color: #3670C7;
    display: inline-block;
}

.footer__block {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: 42px;
    font-family: Lato;
    font-size: 32px;
    font-weight: 700;
    line-height: 42px;
    letter-spacing: 0em;
    text-align: left;
    align-items: center;
    margin-bottom: 40px;
}

.footer__info {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: auto;
}

.footer__list {
    display: flex;
    flex-direction: column;
    row-gap: 15px;
    margin-bottom: 40px;
}

.footer__item {
    font-family: Lato;
    font-size: 22px;
    font-weight: 400;
    line-height: 33px;
    letter-spacing: 0em;
    text-align: left;
}